  background-image: -webkit-linear-gradient(top, #dedede, #e1e1e1);
  background-image: -o-linear-gradient(top, #dedede, #e1e1e1);
  background-image: linear-gradient(to bottom, #dedede, #e1e1e1);
  -webkit-box-shadow: 0px 1px 3px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0px 1px 3px 0 rgba(0, 0, 0, 0.15);
}

.menu__logo {
  height: 32px;
}

.menu__container {
  max-width: var(--max-width);
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}



